import { Component, NgZone, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import version from '../../../../environments/version';
import { AuthService } from '../../../auth/auth.service';
import { ApiService } from '../../../shared/api.service';
import { GsigninService } from '../gsignin.service';

declare const require: (path: string) => any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: [
    './login.component.css',
    '../../../shell/shell/shell.component.css',
  ],
})
export class LoginComponent implements OnInit {
  version = version;
  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private auth: AuthService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private _gsignin: GsigninService,
    private zone: NgZone
  ) {}

  async ngOnInit() {
    await this._gsignin.loadApi();
  }

  async login(username, password) {
    try {
      const token = await this.api
        .post('/api1/login', { username, password })
        .toPromise();
      if (AuthService.validate(token)) {
        this.auth.setToken(token);
        this.router.navigate(['/', 'admin']);
        return;
      }
      this.notificationService.alert(token);
    } catch (error) {
      console.error('error = ', error);
    }
  }
  async register(username, password) {
    const result = await this.api
      .post('/api1/login/register', { username, password })
      .toPromise();
    console.log(result);
    this.notificationService.alert(result);
  }

  assets(image) {
    return require(`./assets/${image}`);
  }

  async test() {
    const accounts = await this.api.get(`/api4/accounts`).toPromise();
    console.log(accounts);
    const account = await this.api
      .get(`/api4/accounts/0cd57d1c-ecfb-499a-be3f-d80f1c9bfff9`)
      .toPromise();
    console.log(account);
    const update = await this.api
      .put(`/api4/accounts/0cd57d1c-ecfb-499a-be3f-d80f1c9bfff9`, {
        username: 'asd',
      })
      .toPromise();
    console.log(update);
  }

  async gsignin() {
    const idtoken = await this._gsignin.signin();
    const token = await this.api.post('/api1/login/g', { idtoken }).toPromise();
    if (AuthService.validate(token)) {
      this.auth.setToken(token);
      this.router.navigate(['/', 'admin']);
      return;
    }
    this.notificationService.alert(token);
  }
}
