import { HttpClient } from '@angular/common/http';
import { Injectable, NgZone } from '@angular/core';
import { loadScript } from 'src/libs/htmlutlis';

@Injectable({
  providedIn: 'root',
})
export class GsigninService {
  constructor(private zone: NgZone, private http: HttpClient) {}

  // https://developers.google.com/identity/sign-in/web/sign-in

  loadApi(): any {
    return new Promise(async res => {
      if (!window['gapi']) {
        await loadScript('https://apis.google.com/js/client.js');
      }
      const gapi = window['gapi'];
      gapi.load('auth2', async () => {
        const auth2 = gapi.auth2.init({
          client_id:
            // '798888321015-fgbnv2i01d53nqs6c5h59vj8kq7ss729.apps.googleusercontent.com',
            '798888321015-e80m33vpi60m9dme02bl9hloplvdnndu.apps.googleusercontent.com',
        });
        res(auth2);
      });
    });
  }

  async signin() {
    const auth2 = await this.loadApi();
    const user = await auth2.signIn();
    const id_token = user.getAuthResponse().id_token;
    return id_token;
  }
}
